import { useContext } from "react";
import { ScrapeContext } from "./ScrapeContext";

export default function Data() {
  const { scrapes } = useContext(ScrapeContext);
  return (
    <div>
      <h2>Your Data:</h2>
      <ul>
        {scrapes.twitter.map(scrape => (
          <li key={scrape.date}>
            {scrape.count}-{}
          </li>
        ))}
      </ul>
      <h1>{scrapes.twitter.length}</h1>
    </div>
  );
}
